// Modular Scale ===============================================================
@import '@fortawesome/fontawesome-free/scss/variables';
@import 'build/sass/functions';
@import 'build/sass/variables';
@import 'build/sass/mixins';

@import 'bootstrap/scss/utilities/embed';

.site-main.landing-page {
    .section-inner {
        @include ratio-space('padding-top' 'padding-bottom', 3, 5);
    }
}

$title-sizes: (
    $sm: 52px,
    $ms: 88px,
    $md: 96px,
    $lg: 122px,
    $xl: 138px,
    $xx: ms(5, 21px)
);

#banner {
    .section-inner {
        background: linear-gradient(
            to top,
            hsla(279, 67, 19, 1),
            ease,
            hsla(279, 67, 19, 0)
        );
    }
    .logo {
        width: 25.8%;
        max-width: none;
        margin-bottom: 0;
        .figure {
            padding-bottom: 60%;
            margin-bottom: 0;
        }
    }
    .title-block {
        padding-bottom: ms(2.5);
        h1 {
            @include poly-fluid-sizing('font-size', $title-sizes);
            span {
                white-space: nowrap;
            }
        }
        .fluid-type {
            font-size: 1em;
            display: block;
            margin-left: auto;
            margin-right: auto;
            &.line-1 {
                font-size: 0.324137931034em;
            }
            &.line-2 {
                font-size: 0.434482758621em;
            }
        }
        .line-3 {
            font-size: 0.172413793103em;
            margin: 1em 0 0.75em;
        }
    }
}

#title-col {
    width: auto !important;
}

#banner-text {
    display: flex;
    flex-direction: column;
    align-items: center;
}

#logo {
    figure {
        margin-bottom: 0;
        display: block;
        width: 100%;
    }
}

#winning {
    background: shade($primary, 30%)
        svg-load('arrow-down.svg', fill=hsla(279, 67, 19, 1)) top center
        no-repeat;
    background-size: 100vw auto;

    @media screen and (orientation: landscape) {
        min-height: 100vh;
    }

    .section-inner {
        padding-top: 0;
    }

    .title-block {
        width: 100%;
        $max: breakpoint-max(ms);
        @media screen and (orientation: portrait) and (max-width: $max) {
            aspect-ratio: '3:2';

            > div {
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                display: flex;
                flex-direction: column;
                align-items: center;
            }
        }
    }

    .content-block {
        @include make-container-max-widths;
        @include ratio-space('padding-bottom', 3, 5);

        padding-left: 2rem;
        padding-right: 2rem;
        .text-line {
            @include media-breakpoint-only(md) {
                display: block;
                margin-left: auto;
                margin-right: auto;
                &.line-1 {
                    width: 100%;
                }
                &.line-2 {
                    width: 67%;
                }
                &.line-3 {
                    width: 33%;
                }
            }
        }
    }
}

#tiers-intro {
    overflow: hidden;

    .title-col {
        > .content-wrapper {
            @include ratio-space('padding-top' 'padding-bottom', 3, 5);

            // padding-left: 2rem;
            // padding-right: 2rem;

            // @each $breakpoint, $max-width in $container-max-widths {
            //     $gutter: 100vw - $max-width;

            //     @if $gutter !=0 {
            //         @include media-breakpoint-up(
            //             $breakpoint,
            //             $grid-breakpoints
            //         ) {
            //             padding-left: $gutter/2;
            //             padding-right: $gutter/2;
            //         }
            //     }
            // }
        }

        .large {
            @include media-breakpoint-up(lg) {
                transform: translateX(16%);
            }
        }
    }

    .content-col {
        > .content-wrapper {
            @include ratio-space('padding-top' 'padding-bottom', 3, 5);

            padding-left: 2rem;
            padding-right: 2rem;
            background-color: $info;

            @include media-breakpoint-up(lg) {
                background-color: transparent;
            }
            &::before {
                @include media-breakpoint-up(lg) {
                    content: '';
                    display: block;
                    position: absolute;
                    top: 0;
                    left: -12%;
                    width: 100vw;
                    height: 100%;
                    background-color: transparent;
                    background-image: svg-load('light-shape.svg', fill=$info);
                    background-position: left top;
                    background-repeat: no-repeat;
                    background-size: auto 100%;
                }
            }

            @each $breakpoint, $max-width in $container-max-widths {
                $gutter: 100vw - $max-width;

                @if $gutter !=0 {
                    @include media-breakpoint-up(
                        $breakpoint,
                        $grid-breakpoints
                    ) {
                        padding-left: $gutter/2;
                        padding-right: $gutter/2;
                    }
                }
            }
        }
    }
}

.tiers {
    border-top: solid 1px #fff;

    .title-col {
        z-index: 2;

        > .content-wrapper {
            @include ratio-space('padding-top', 3, 5);

            padding-left: 2rem;
            padding-right: 2rem;
            height: calc(100% + 2px);
            margin-bottom: -2px;

            @include media-breakpoint-up(ml) {
                width: calc(100% + 4px);
                margin: 0 -2px;
                padding-left: 0;
                padding-right: 0;
            }
        }
    }

    .content-col {
        z-index: 1;

        > .content-wrapper {
            @include ratio-space('padding-top' 'padding-bottom', 3, 5);

            padding-left: 2rem;
            padding-right: 2rem;

            @each $breakpoint, $max-width in $container-max-widths {
                $gutter: 100vw - $max-width;

                @if $gutter !=0 {
                    @include media-breakpoint-up(
                        $breakpoint,
                        $grid-breakpoints
                    ) {
                        padding-left: $gutter/2;
                        padding-right: $gutter/2;
                    }
                }
            }
        }

        .tier-price {
            .h3 {
                margin-bottom: ms(-1);
            }
        }

        .tier-description {
            margin-bottom: ms(1);
        }

        .tier-item {
            padding-top: ms(2);

            .h6 {
                margin-bottom: ms(0);
            }

            .content-wrapper {
                @include media-breakpoint-up(ml) {
                    flex-direction: row;
                }
            }
        }

        .tier-item-icon {
            margin-bottom: ms(1);

            @include media-breakpoint-up(ml) {
                margin-bottom: 0;
                margin-right: ms(1);
            }

            .figure {
                width: ms(4);
                height: ms(4);
                margin-left: auto;
                margin-right: auto;
                margin-bottom: 0;
                display: flex;
                > div {
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    flex: 1 0 100%;
                    width: 100%;
                    height: 100%;
                    svg {
                        flex: 1 1 auto;
                        max-width: 100%;
                        max-height: 100%;
                    }
                }
            }
        }
    }
}

#tier-1 {
    background-color: hsla(280, 29, 35, 1);

    .title-col {
        > .content-wrapper {
            background: linear-gradient(
                to bottom,
                hsla(280, 29, 35, 0.33),
                ease,
                hsla(280, 29, 35, 1)
            );

            @include media-breakpoint-up($d-bp) {
                background: linear-gradient(
                    to right,
                    hsla(280, 29, 35, 0.33),
                    ease,
                    hsla(280, 29, 35, 1)
                );
            }
        }
    }

    .content-col {
        > .content-wrapper {
            background-color: hsla(280, 29, 35, 1);
        }
    }
}

#tier-2 {
    background-color: hsla(280, 42, 27, 1);

    .title-col {
        > .content-wrapper {
            background: linear-gradient(
                to bottom,
                hsla(280, 42, 27, 0.33),
                ease,
                hsla(280, 42, 27, 1)
            );

            @include media-breakpoint-up($d-bp) {
                background: linear-gradient(
                    to left,
                    hsla(280, 42, 27, 0.33),
                    ease,
                    hsla(280, 42, 27, 1)
                );
            }
        }
    }
}

#tier-3 {
    background-color: hsla(279, 67, 19, 1);

    .title-col {
        > .content-wrapper {
            background: linear-gradient(
                to bottom,
                hsla(279, 67, 19, 0.33),
                ease,
                hsla(279, 67, 19, 1)
            );

            @include media-breakpoint-up($d-bp) {
                background: linear-gradient(
                    to right,
                    hsla(279, 67, 19, 0.33),
                    ease,
                    hsla(279, 67, 19, 1)
                );
            }
        }
    }
}

#three-uniques {
    h3 {
        color: $primary;
    }

    .number-bg {
        @include text-crop(1);

        display: block;
        z-index: -1;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        font-size: ms(6);
        color: rgba($info, 0.2);
        font-weight: $headings-font-weight;

        .dot {
            @include sr-only;
        }
    }
}

#cta {
    .section-inner {
        background-color: rgba(#000, 0.5);
    }
}

#contact-us {
    .form-control,
    .select2-container--bootstrap4 .select2-selection,
    .custom-control-icon {
        box-shadow: none;
        background-color: rgba(#fff, 0.5);
    }
    h5,
    .h5 {
        color: $secondary;
    }
    .text-muted {
        color: $info !important;
    }
    textarea#practice-areas,
    .states-barred > .select2 {
        min-height: calc(
            #{$input-line-height * 1rem} * 2 + #{$input-padding-y * 2} * 2 + #{$input-height-border}
        );
    }
    .tiers {
        border-top: none;
        .radio {
            margin: 0 $gutters-50 $gutters-50 0;
        }
    }
    .preferred-form-of-comm {
        .radio {
            width: 50%;
        }
    }
}

#twitter-feed {
    .content-item {
        display: flex;
        flex-direction: column;
    }
    .maat-twitter-feed {
        flex: 1 1 auto;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
    }
    .maat-twitter-feed-header {
        margin-bottom: ms(1);

        .h5 {
            display: flex;
            align-items: center;
            i {
                font-weight: normal !important;
                display: block;
                margin-right: ms(0);
            }
        }
    }

    .maat-tweets {
        display: flex;
        flex-direction: column;
        justify-content: center;
    }

    .maat-twitter-feed-footer {
        margin-top: ms(1);
    }

    .maat-tweet {
        border-bottom: 1px solid rgba($dark, 0.33);
        padding-bottom: ms(0);
        margin-bottom: ms(0);
        &:last-child {
            border-bottom: none;
            padding-bottom: 0;
            margin-bottom: 0;
        }

        .maat-tweet-txt {
            margin-bottom: ms(-1);
        }
    }
    .maat-tweet-meta {
        display: flex;
        align-items: baseline;
        color: rgba($dark, 0.75);
        i {
            margin-right: 0.25rem;
            color: rgba($dark, 0.75);
        }
        span {
            color: rgba($dark, 0.75);
        }
    }
    a {
        @include hover-focus-active {
            color: #fff;
        }
    }
}

#instagram-feed {
    #sb_instagram {
        overflow: hidden !important;
        position: relative;
        &::after {
            display: none;
        }
        #sbi_load {
            display: none !important;
        }
        #sbi_images {
            @include make-row($gutter: $gutters-50);

            width: calc(100% + #{$gutters-50}) !important;
            float: none;
            .sbi_item {
                @include make-col-ready($gutter: $gutters-50);
                @include make-col(6);

                padding-right: $gutters-50 / 2 !important;
                padding-left: $gutters-50 / 2 !important;
                margin-bottom: $gutters-50 !important;
                .sbi_photo {
                    @extend .embed-responsive;
                    @extend .embed-responsive-1by1;

                    height: auto !important;
                    opacity: 1 !important;

                    @include hover-focus {
                        &::before,
                        &::after {
                            transform: translateY(100%);
                        }
                    }

                    &::before {
                        transition: all 0.7s easeOutQuint;
                        background: linear-gradient(
                            to bottom,
                            hsla(279, 67, 19, 0),
                            ease,
                            hsla(279, 67, 19, 0.8)
                        );
                    }

                    &::after {
                        @include icon;

                        transition: all 0.7s easeOutQuint;
                        content: icon($fa-var-instagram);
                        font-family: 'Font Awesome 5 Brands';
                        color: #fff;
                        font-size: ms(1);
                        position: absolute;
                        bottom: 0;
                        right: 0;
                        padding: ms(0);
                    }
                }
            }
        }
    }
}

#footer {
    .social-list-item {
        a {
            font-size: ms(2);
            padding: 0 0.5rem;
            @include hover-focus-active {
                background-color: transparent !important;

                i {
                    color: $secondary;
                }
            }
        }
    }
    .schema-info {
        font-weight: $font-weight-light;
        &::before {
            @include icon;

            font-size: ms(1);
            padding-right: 0.5rem;
            color: $info;
            font-family: 'Maat-Legal';
        }
        &.phone {
            &::before {
                content: icon($maat-circle-phone);
            }
        }
        &.email {
            &::before {
                content: icon($maat-circle-email);
            }
        }
    }
}

#footer-logo {
    width: ms(5);
}
